.tools__wrapper {
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    width: 100px;
    padding:0px;
}
.tool {
    width: 46px;
    height: 46px;
    background: #2d2d2d;
    margin: 1px;
    box-sizing: border-box;
    background-repeat: no-repeat;
    background-size: 60%;
    background-position-x: 10px;
    background-position-y: 10px;
}
.tool--fullsize {
    background-size: 100%;
    background-position-x: 0;
    background-position-y: 0;
}
.hidden {
    display: none;
}
.tool--active {
    border: 3px solid gold;
}
.tool__pen {
    background-image: url('../../image/icons/pencil.png');
}
.tool__pen-mirror {
    background-image: url('../../image/icons/tool-mirror-pen@2x.png');
}
.tool__paint--bucket{
    background-image: url('../../image/icons/paint-bucket.png');
}
.tool_paint--all {
    background-image: url('../../image/icons/tool-colorall@2x.png');
}
.tool__eraser {
    background-image: url('../../image/icons/eraser.png');
}
.tool__stroke {
    background-image: url('../../image/icons/tool-stroke.png');
}
.tool__rectangle {
    background-image: url('../../image/icons/rectangle.png');
}
.tool__circle {
    background-image: url('../../image/icons/oval.png');
}
.tool__move {
    background-image: url('../../image/icons/hold.png');
}
.tool_shape {
    background-image: url('../../image/icons/magic-wand.png');
}
.tool__rectangle--selection {
    background-image: url('../../image/icons/selection.png');
}
.tool__lasso {
    background-image: url('../../image/icons/lasso.png');
}
.tool__lighten {
    background-image: url('../../image/icons/compensation.png');
}
.tool__dithering {
    background-image: url('../../image/icons/square-pattern.png');
}
.tool__color--picker {
    background-image: url('../../image/icons/color-picker.png');
}
