.tools__wrapper--overview {
    width: 100%;
    display: flex;
    justify-content: center;
}
.container--overview {
    margin: 120px 0;
    padding: 0 30px;
}
.title--overview {
    font-size: 4em;
}
.item__container {
    display: flex;
    flex-wrap: wrap;
    margin: 0 auto;
    max-width: 1400px;
    margin-top: 20px;
}
.item__wrapper {
    width: 350px;
    display: flex;
}
.tool--overview {
    width: 100px;
    height: 100px;
    background-position-x: 21px;
    background-position-y: 25px;
}
.tool--fullsize {
    background-size: 100%;
    background-position-x: 0;
    background-position-y: 0;
}
.tool__description {
    color: #ffff;
    font-size: 2em;
    margin-left: 10px;
    margin-top: 35px;
}
.tools__wrapper {
    margin-top: 20px;
}
.overview__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.assets__container {
    display: flex;
    justify-content: space-between;
    max-width: 80%;
    margin-top: 100px;
}
.assets__column--left {
    text-align: right;
}
.assets__column--right {
    direction: rtl;
    text-align: left;
}
.assets__column { 
    width: 49%;
}
.assets__wrapper {
    width: 65%;
    margin: 0 20px;
}
.assets__item {
    display: flex;
    margin-top: 20px;
}
.image__wrapper--assets {
    width: 220px; 
    height: 200px;
    background:#fff;
}
.assets__title {
    margin: 0 0 15px 0;
    color: #ffff;
    font-size: 2.5em;
}
.assets__description {
    color: #999999;
    font-size: 1.15em;
    font-weight: 700;
}
.assets__image {
    width: 100%;
    height: 100%;
}
