.header__container {
    background: #8229B3;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 2%;
    height: 50px;
    margin-bottom: 10px;
    width: 96vw;
}
.header__title {
    color: #fff;
    font-size: 1.7em;
}
.header__name {
    color:#808080;
    font-size: 1.7em;
}
.button__wrapper {
    width: 160px;
    display: flex;
    justify-content: space-between;
}
.button {
    height: 25px;
    border: none;
    border-radius: 3px;
    font-weight: 700;
}
.button__header {
    background-color: #ffd700;
    text-decoration: none;
    height: 25px;
    padding: 3px;
    color: black;
    padding: 5px 5px 0 7px;
}
.logo__link {
    text-decoration: none;
    color: white;
}
