input[type="color"] {
	opacity: 0;
	display: block;
	width: 40px;
	height: 40px;
	border: none;
}
.palette__wrapper {
    position: relative;
    margin-left: 15px;
}
.color__wrapper {
    width: 40px;
    height: 40px;
    background-color: attr(data-color);
    overflow: hidden;
    margin-top: 10px;
    position: absolute;
    border: 2px solid black;
}
.color__wrapper--primary {
    z-index: 5;
}
.color__wrapper--secondary {
    top: 25px;
    left: 20px;
    z-index: 3;
}
.color__primary {
    z-index: 9;
}
.color__secondary {
    top: 30px;
    left: 30px;
    z-index: 7;
}

