.container--examples {
    margin: 80px auto;
}
.examples__wrapper {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.title--example {
    font-size: 4em;
    font-weight: 500;
    color: #ffff;
}
.examples {
    display: flex;
    width: 94%;
    justify-content: space-between;
    margin-top: 20px;
    flex-wrap: wrap;
}
.example {
    width: 220px;
    display: flex;
    flex-direction: column;
    border: 5px solid gold;
}
.discription--example {
    background-color: #666;
    font-size: 2em;
    font-weight: 500;
    padding-left: 15px;
}
.image--example {
    width: 220px;
    height: 200px;
}
.grow:hover {
    -webkit-transform: scale(1.2);
    -ms-transform: scale(1.2);
    transform: scale(1.2);
    transition: 0.7s;
}
