.main {
    display: flex;
    width:85%;
}
.main__container {
    display: flex;
    overflow: hidden;
    height: 90vh;
}
.column__wrapper {
    display: flex;
    width: 100%;
}
.column__left {
    width: 200px;
    height: 100%;
    display: flex;
    justify-content: center;
}
.column__main {
    width: 100%;
    height: 100%;
    margin-right: 10px;
}
.column__right {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 220px;
    height: 100%;
}
