.modal__overlay {
    background-color: rgba(0, 0, 0, 0.5);
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  }
  
  .modal__window {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #FFF;
    min-height: 500px;
    width: 400px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  
  .modal__header,
  .modal__body,
  .modal__footer {
    padding: 5px 20px;
  }
  
  .modal__header {
    min-height: 20px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    display: flex;
    justify-content: space-between;
    box-sizing: border-box;
    align-items: center;
  }
  
  .modal__title {
    font-weight: bold;
  }
  
  .modal__footer {
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }
  
  .modal__footer button:last-child {
    margin-left: 10px;
  }
