.container--about {
    margin: 105px auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 40px;
}
.title__wrapper {
    width: 30%;
    display: flex;
    flex-direction: column;
    height: 90%;    
    justify-content: center;
}
.image__wrapper--about {
    width: 65%;
    display: flex;
    margin-right: 30px;
}
.title {
    font-size: 5.5em;
    color: #ffff;
    margin: 0;
}
.title__description {
    font-size: 1.5em;
    color: #ffff;
}
.image--about {
    width: 100%;
    border: 7px solid gold;
}
.button__wrapper--about {
    width: 100%;
    display: flex;
    justify-content: center;
    margin-top: 20px;
}
.button--about {
    height: 80px;
    font-size: 3.5em;
    margin-right: 60px;
}
