.frames__container {
    width: 130px;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.frame__list {
    display: flex;
    flex-direction: column;
    padding: 0;
    max-height: calc(100vh - 30px);
    width: 140px;
    list-style: none;
    overflow-y: scroll;
    overflow-x: hidden;
    max-height: 80vh;
}
.frame__item {
    position: relative;
    order: attr(data-order);
}
.frame {
    border: 1px solid black;
    margin-top: 10px;
    width: 90%;
    background: #fff;
}
.frame--active {
    border: 3px solid gold;
}
.frame__wrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.tolltip__button{
    position: absolute;
    width: 30px;
    height: 30px;
    background: transparent;
    border: none;
    font-size: 18px;
    font-weight: 600;
    background: #666;
    background-size: 65%;
    background-position-x: 5px;
    background-position-y: 5px;
}
.tooltip__button--active {
    color: white;
    background-color: gold;
}
.tolltip__button--number {
    top: 12px;
    left: 6px;
}
.tolltip__button--delete {
    top: 12px;
    right: 4px;
    z-index: 100;
    background-image: url('../../image/icons/trash.png');
    background-repeat:  no-repeat;
}
.tolltip__button--drag {
    bottom: 2px;
    left: 6px;
    background-image: url('../../image/icons/move-arrows.png');
    background-repeat:  no-repeat;
}
.tolltip__button--dublicate {
    bottom: 2px;
    right: 4px;
    background-image: url('../../image/icons/copy-document.png');
    background-repeat:  no-repeat;
}
.frames__button {
    display: flex;
    justify-content: space-around;
    border: 3px solid #888888;
    border-radius: 5px;
    width: 80%;
}
.frames__label {
    height: 40px;
    width: 60px;
    color: #fff;
}
.icon__plus {
    width: 30px;
    height: 30px;
    background-image: url('../../image/icons/add.png');
    background-repeat:  no-repeat;
    background-size: 75%;
    background-position-x: 5px;
    background-position-y: 7px;
}
::-webkit-scrollbar-button { 
    background-repeat:no-repeat; 
    width:6px; 
    height:0px 
    } 
    
::-webkit-scrollbar-track { 
    background-color:#32312e; 
    box-shadow:0px 0px 3px #000 inset; 
} 
    
::-webkit-scrollbar-thumb { 
    -webkit-border-radius: 5px; 
    border-radius: 5px; 
    background-color:#ffcb17; 
    box-shadow:0px 1px 1px #fff inset; 
    background-image:url('https://yraaa.ru/_pu/24/59610063.png'); 
    background-position:center; 
    background-repeat:no-repeat; 
} 
    
::-webkit-resizer{ 
    background-repeat:no-repeat; 
    width:7px; 
    height:0px 
} 
    
::-webkit-scrollbar{ 
    width: 11px; 
}
