.layersPanel__container {
    width: 100%;
    border: 2px solid #8229B3;
    box-sizing: border-box;
    padding: 0 10px;
    margin-top: 10px;
    color: white;
}
.layersPanel__title {
    margin: 0;
}
.buttons__container--layers {
    display: flex;
    justify-content: space-between;
    margin-top: 10px;

}
.layers__name {
    pointer-events: none;
}
.layersList__item {
    cursor: pointer;
}
.item--active {
    color: gold;
}
.button__layers {
    width: 20px; 
    height: 20px;
    padding: 0;
    border: none;
}
.button__layers:hover {
    border: 1px solid gold;
}
.layersList__wrapper {
    height: 60px;
    overflow-y: scroll;
    list-style: none;
    padding: 0;
    margin: 10px 0 0 0;
}
.button__layers--plus {
    background: url('../../../image/icons/add.png');
    background-size: 100%;
}
.button__layers--up {
    background: url('../../../image/icons/up.png');
    background-size: 100%;
}
.button__layers--down {
    background: url('../../../image/icons/down.png');
    background-size: 100%;
}
.button__layers--delete {
    background: url('../../../image/icons/delete.png');
    background-size: 100%;
}

