.pensize__wrapper {
    display: flex;    
}
.pensize__option {
    width: 20px;
    height: 20px;
    background-color: black;
    border: 2px solid #666;
    position: relative;
}
.pensize__option::before {
    content: "";
    background: #ffff;
    position: absolute;
}
[data-size='1']::before{
    width: 6px;
    height:6px;
    top: 31%;
    left: 33%;
}
[data-size='2']::before{
    width: 9px;
    height:9px;
    top: 25%;
    left: 28%;
}
[data-size='3']::before{
    width: 12px;
    height:12px;
    top: 21%;
    left: 22%;
}
[data-size='4']::before{
    width: 14px;
    height:14px;
    top: 14%;
    left: 14%;
}
.pensize__option--active  {
    border: 2px solid gold;
}
