.canvas, .preview__canvas {
    background-image: url('../../image/bg.png');
    image-rendering: pixelated;
}
.canvas__drawing {
    transform: scale(0.8);
    position: absolute;
    cursor: crosshair;
}
.canvas--layer {
    transform: scale(0.8);
    display: flex;
    pointer-events: none;
    position: absolute;
    background: none;
}
.layers__container {
    width: 100%;
    height: 100%;
    position: relative;
    display: flex;
    justify-content: center;
    background:#a0a0a0;
    align-items: center;
}
.layer__wrapper{ 
    position: absolute;
    display: flex;
}
.layer__wrapper--layer {
    pointer-events: none;
}
.layer--active {
    color: gold;
}
