.container--footer {
    background: black;
    height: 50px;
    width: 100vw;
    margin: 0;
    color: #ffff;
    display: flex;
    justify-content: space-between;
}
.footer__title {
    margin: 0;
    text-align: right;
    display: block;
    margin-left: 20px;
}
.social {
    display: flex;
    margin-right: 20px;
    height: 100%;
    align-items: center;
    width: 130px;
    justify-content: space-between;
}
.social__link {
    text-decoration: none;
    color: #fff;
    width: 40px;
    height: 40px;
}
.info {
    display: flex;
    margin-left: 70px;
    margin-top: 15px;
}
.info__year {
    display: block;
    text-align: right;
}
.social__img {
    width: 100%;
    height: 100%;
}
