.transfrom__container {
    width: 100%;
    border: 2px solid  #8229B3;
    box-sizing: border-box;
    padding: 0 10px;
    margin-top: 10px;
    color: white;
}
.transform__title {
    margin: 0 0 10px 0;
}
.tools__wrapper--transform {
    justify-content: center;
    margin: 0;
}
.tool__flip{
    background-image: url('../../image/icons/tool-flip.png');
}
.tool__rotate{
    background-image: url('../../image/icons/tool-rotate.png');
}
