/* ВНЕШНИЙ БЛОК */
.settings__container {
    position: fixed;
    bottom: 50px;
    right: 0;
    background: gold;
    width: 30px;
    height: 50px;
    font: 14px Arial;
    color: #fff;
    text-align: center;
    padding: 5px;

    -webkit-transition-duration: 0.3s; 
    -moz-transition-duration: 0.3s;
    -o-transition-duration: 0.3s;
    transition-duration: 0.3s;
    border: 1px solid #8229B3;
    -webkit-border-radius: 5px 0 0 5px; 
    -moz-border-radius: 5px 0 0 5px;
    border-radius: 5px 0 0 5px;
    }
.setting__hidden {
    position: fixed;
    bottom: 40px;
    right: -220px; 
    background: #8229B3;
    color: #000; 
    width: 170px;
    height: 280px;
    padding: 10px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    font-size: 1.2em;
    font-weight: 600;
    color: #ffff;
    padding: 20px;

    -webkit-transition-duration: 0.3s; 
    -moz-transition-duration: 0.3s;
    -o-transition-duration: 0.3s;
    transition-duration: 0.3s;

    -webkit-border-radius: 5px 0 0 5px; 
    -moz-border-radius: 5px 0 0 5px;
    border-radius: 5px 0 0 5px;
    }
.settings__container:hover {
    right: 210px; 
}
.settings__container:hover .setting__hidden {
    right: 0; 
}
.setting__label {
    width: 70px;
    text-align: left;
}
.resize__section {
    width: 150px;
    display: flex;
}
.button__export {
    margin-top: 20px;
}
.input__text {
    width: 40px;
}
.input__wrapper {
    margin-top: 10px;
}
.button__keyboard {
    width: 70px;
    height: 30px;
    position: absolute;
    bottom: 15px;
    right: 65px;
    background: url('../../image/icons/keyboard.png') no-repeat;
    background-position-x: 10px;
    background-position-y: -15px;
    border: none;
}
.modal__cancel {
    background: url('../../image/icons/delete.png');
    background-size: 100%;
    width: 15px;
    height: 15px;
    border: none;
    
}
